class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user: '123',
      money: '0.00',
      tableLoading: false,
      selectedRowKeys: [],
      visible: false,
      pageNo: 1,
      gameFormData:{}
    }
    this.appManage = this.appManage.bind(this)
    // this.addGame = this.addGame.bind(this)
    this.onShowSizeChange = this.onShowSizeChange.bind(this)
    this.changePageNo = this.changePageNo.bind(this)
  }
  onSelectChange = (selectedRowKeys) => {
    // console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  }
  appManage(v){
    v?this.props.history.push({pathname:"/handleGame",query: v}):this.props.history.push('/handleGame')
  }
  changePageNo(page, pagesize) {// 用来监听pageno变化
    console.log(page)
    console.log(pagesize)
  }
  onShowSizeChange(current, pageSize) { // 用来监听pagesize变化
    console.log(current, pageSize);
  }
  // addGame = (v) => {
  //   this.setState({
  //     gameFormData:{}
  //   })
  //   this.setState({ visible: true })
  //   this.props.history.push({path:'/handleGame',value:v})
  // }
  hideModal = () => {
    const _this = this
    Modal.confirm({
      title: 'Confirm',
      content: 'Bla bla ...',
      okText: '确认',
      cancelText: '取消',
      onOk() {
        _this.setState({ visible: false })
      },
    });
  }
  render() {
    const { tableLoading,gameFormData, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div style={styles.bg} className='home fadein'>
        <div className='info'>
          <Row >
            <Col span={14}>
              <Card
                title="开发者信息"
                extra={<em className='cursor' href="#">编辑</em>}
              >
                <p>欢迎回来，</p>
                <h1>北京三金宇通爱上电视</h1>
                <p>上次登录时间{this.state.user}</p>
              </Card>
            </Col>
            <Col span={10}>
              <Card
                title="财务情况"
                className='money ml20'
                extra={<div><em className='cursor'>明细</em><em className='cursor' style={{ marginLeft: '7px' }}>编辑</em></div>}
              >
                {/* <p>账户余额：<span>{this.state.money}</span> 元</p>
                <p>昨日消耗：<span>{this.state.money}</span> 元</p> */}
                <p className="cursor">明细</p>
                <p className="cursor">编辑</p>
              </Card>
            </Col>
          </Row>
        </div>
        <div className='gameCon'>
          <div>
            <Button type="primary" onClick={()=>this.appManage()}>添加应用</Button>
          </div>
          <div className="mt20">
            <Table loading={this.state.tableLoading} pagination={false} className="nopadtable" columns={columns(this.appManage)} dataSource={data} />,
            <div className="mb20" style={{ textAlign: "right" }}>
              <Pagination showSizeChanger onChange={this.changePageNo} onShowSizeChange={this.onShowSizeChange} defaultCurrent={this.state.pageNo} total={500} />
            </div>
          </div>
        </div>
        <Modal
          title="添加应用"
          width='40%'
          destroyOnClose
          visible={this.state.visible}
          onCancel={this.hideModal}
          footer={[]}
        >
          <UploadGame gameFormData={gameFormData}/>
        </Modal>
      </div>
    )
  }
}

const styles = {
  bg: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: 'calc(100vh - 64px)'
  }
}

export default Home



