import React from 'react'
import { Icon, Button, Upload, message } from 'antd';
import { uploadimg } from '../../api/login' // 封装的上传路径

class FileUpload extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      fileList: []
    }
    this.removeFile = this.removeFile.bind(this)
    this.customRequest = this.customRequest.bind(this)
  }
  removeFile(file){
    console.log(file)
    this.state.fileList.map((v,i)=>{
      if(file.uid === v.uid){
        this.state.fileList.splice(i,1)
        this.setState({fileList:this.state.fileList})
        this.props.onChange(this.state.fileList)
      }
    })
  }
  componentDidMount(){
    this.props.onRef(this)
  }
  getImgFiles(v){
    this.setState({ fileList:v.fileList||[] })
  }
  customRequest = (option) => {
    const that = this
    const formData = new FormData();
    formData.append('photo', option.file);
    uploadimg(formData).then(res => {
      that.state.fileList.push({
        name: 'yyy.png',
        status: 'done',
        url: res.data[0].file_url,
      })
      that.state.fileList.map((v, i) => {
        v.uid = i
      })
      that.setState({
        fileList: that.state.fileList
      })
      that.props.onChange(that.state.fileList)
    })
  }
  render() {
    const Dragger = Upload.Dragger;
    const fileList = this.state.fileList
    return (
      <Dragger
        // accept="application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
        customRequest={this.customRequest}
        // beforeUpload={this.beforeUpload}
        onRemove = {this.removeFile}
        fileList={fileList}
      >
        <p className="ant-upload-drag-icon">
          <Icon type="inbox" />
        </p>
        <p className="ant-upload-text">点击上传apk</p>        
      </Dragger>
    )
  }
}

export default FileUpload



