import React from 'react'
import { Form, Icon, Input, Button, Checkbox, Upload, message ,Steps} from 'antd';
import { uploadimg } from '../../../api/login'
import FileUpload from '../../../components/FileUploadBlock'
@Form.create()
class UploadGame extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      fileList: [],
      isEdit:true,
      gameFormData:this.props.gameFormData,
      current: 0
    }
  }
  onRef=(ref)=>{
    this.uploadCon = ref
  }
  componentDidMount(){
    console.log(this.props.gameFormData)
    this.uploadCon.getImgFiles(this.props.gameFormData)
  }
  handlePhotoChange(val) {
    console.log(val)
  }
  render() {
    const { current  } = this.state
    const Step = Steps.Step;
    const steps = [{
      title: '填写应用信息',
    }, {
      title: '应用审核中',
    }, {
      title: '应用上架',
    }];
    return (
      <div>
        <Steps current={current}>
          {steps.map(item => <Step key={item.title} title={item.title} />)}
        </Steps>
        <br/>
        <FileUpload onChange={this.handlePhotoChange} onRef={this.onRef.bind(this)}/>
        <div>
          <Button>确定</Button>
        </div>
      </div>
    )
  }
}

export default UploadGame



